body::-webkit-scrollbar {
  width: 0.75em;
}

body::-webkit-scrollbar-thumb {
  background-color: rgb(255, 149, 110, 0.75);
  outline: 1px solid rgb(255, 149, 110);
}

.book-pane {
  height: 80vh;
  max-height: 80vh;
  display: flex;
}
.book-pane .booklist {
  flex: 0 0 65%;
  padding: 1em;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.book-pane .book-details {
  flex: 0 0 27.5%;
  padding: 1em;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
  max-height: 100%;
}
.book-pane .book-details ul {
  margin-top: 0.5rem;
  margin-left: 2.5rem;
}
.book-details p {
  font-size: 1.25em;
  margin: 0.5em 0;
}
.book-details p span {
  font-weight: bold;
}
.no-book {
  align-items: center;
  display: flex;
  justify-content: center;
  width: 100%;
  font-size: 2em;
}
.booklist h1 {
  font-size: 3em;
}
.booklist ul {
  display: flex;
  flex-wrap: wrap;
  overflow-y: auto;
  margin: 0;
  padding: 1em;
}
.booklist ul::-webkit-scrollbar,
.book-details::-webkit-scrollbar {
  width: 0.25em;
}
.booklist ul::-webkit-scrollbar-thumb,
.book-details::-webkit-scrollbar-thumb {
  background-color: rgb(255, 149, 110, 0.75);
  outline: 1px solid rgb(255, 149, 110);
}
.booklist li {
  list-style: none;
  padding: 0.5em 1em;
  font-size: 1.25em;
  font-weight: 600;
}
.booklist li span {
  color: red;
}
.booklist li .close-icon {
  color: red;
  padding-left: 0.5em;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.booklist li:hover,
.booklist li:focus {
  cursor: pointer;
  animation: pulse 1s;
  box-shadow: 0 0 0 1em rgba(255, 255, 255, 0);
}

.add-book-form {
  display: flex;
  justify-content: space-evenly;
  font-size: 1.5em;
  align-items: center;
}
.add-book-zone {
  width: 100%;
  border-top: 2px dashed;
  padding: 1em 0;
  display: flex;
  flex-direction: column;
}
.add-book-zone h2 {
  margin-top: 0;
}
.add-book-form .field {
  display: flex;
  flex-direction: column;
  position: relative;
}
.add-book-form .field label {
  padding-right: 0.25em;
}
.add-book-form .field label {
  padding-right: 0.25em;
}
.add-book-form .field input,
.add-book-form .field select {
  border: 0;
  padding: 8px 0 8px 4px;
  border-bottom: 1px solid #ccc;
  font-size: 0.75em;
  font-family: "Dancing Script", cursive;
  background-color: transparent;
}
.add-book-form .field input ~ .focus-border,
.add-book-form .field select ~ .focus-border {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: rgb(255, 149, 110);
  transition: 0.4s;
}
:focus-visible {
  outline: none;
}
.add-book-form .field input:focus ~ .focus-border,
.add-book-form .field select:focus ~ .focus-border {
  width: 100%;
  transition: 0.4s;
}

.add-book-form button {
  background-color: rgb(255, 149, 110);
  /* color: #0b0c10; */
  padding: 2em 2em;
  height: 1em;
  width: 1em;
  border-radius: 100%;
  bottom: 1rem;
  right: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-color: transparent !important;
}
.add-book-form button span {
  transition: all 0.5s ease-in-out;
}

.add-book-form button:hover {
  transform: scale(0.9);
  box-shadow: 0px 0px 0px 2px white, 0px 0px 0px 12px #ffc46c;
  cursor: pointer;
}

.add-book-form button:hover span {
  transform: scale(1.5);
}
.toggle-section {
  cursor: pointer;
  font-size: 1.25em;
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgb(255, 149, 110, 0.75);
  }
}
